<template>
  <div class="lwh_exercise">
    <!-- 头部 -->
    <header>练习</header>
    <!-- 滑动部分 -->
    <div class="lwh_bet_scro">
      <div>
        <!-- 下拉刷新 -->
        <div class="lwh_top">{{ lwh_cont }}</div>
        <!-- 考点练习 -->
        <div class="lwh_list">
          <ul>
            <li v-for="(item, index) in lwh_list" @click='wsy_jump(item.path)' :key="index">
              <div>
                <van-icon :name="item.icon" :color="item.color" size="0.2rem" />
              </div>
              <p>{{ item.cont }}</p>
            </li>
          </ul>
        </div>
        <!-- 默认排序 -->
        <div class="lwh_cont">
          <p>近期模考</p>
          <div>
            <p :class="{ lwh_default: lwh_flag }" @click="lwh_flag = true">默认排序</p>
            <p :class="{ lwh_default: !lwh_flag }" @click="lwh_btn()">
              时间
              <span :class="{ lwh_span_defaule: lwh_span_flag }">
                <img
                  src="data:image/png;base64,"
                  alt
                />
                <img
                  src="data:image/png;base64,"
                  alt
                />
              </span>
            </p>
          </div>
          <!-- 数据显示 -->
          <div class="lwh_main">
            <img src="https://wap.365msmk.com/img/empty@2x.a2fa524d.png" alt />
            <p>暂无模考安排，敬请期待</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BetterScroll from "better-scroll";
export default {
  name: "",
  // 组件注册
  components: {},
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      lwh_bs: null,
      lwh_cont: "下来即可刷新",
      lwh_list: [
        {
          cont: "专点专练",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/point"
        },
        {
          cont: "套卷练习",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/paper-package"
        },
        {
          cont: "仿真模考",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/exam"
        },
        {
          cont: "错题练习",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/error-ques"
        },
        {
          cont: "测评记录",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/assess"
        },
        {
          cont: "习题收藏",
          icon:
            "data:image/png;base64,",
          color: "#26bbd0",
          path: "/ques-collect"
        }
      ],
      lwh_flag: true,
      lwh_span_flag: false
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    // 滑动插件
    this.$nextTick(() => {
      this.lwh_bs = new BetterScroll(".lwh_bet_scro", {
        probeType: 2,
        click: true
      });
      // 上滑禁止
      this.lwh_bs.on("scroll", pos => {
        if (pos.y < 0) {
          this.lwh_bs.scrollTo(0, 0);
        }
        if (pos.y > 50) {
          this.lwh_cont = "松手即可刷新";
        }
      });
    });
  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    lwh_btn() {
      this.lwh_flag = false;
      this.lwh_span_flag = !this.lwh_span_flag;
    },
    // 跳转
    wsy_jump(path){
      this.$router.push(path)
    }
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {},
  // 监听属性
  watch: {}
};
</script>

<style lang="scss" scoped>
.lwh_exercise {
  width: 100%;
  height: 100%;
  background: #f0f2f5;
  > header {
    width: 100%;
    height: 0.46rem;
    text-align: center;
    line-height: 0.46rem;
    background: #fff;
    font-size: 0.2rem;
    color: #595959;
  }
}
.lwh_bet_scro {
  width: 100%;
  height: 4.57rem;
  overflow: hidden;
  > div {
    width: 100%;
    min-height: 101%;
    position: relative;
    > .lwh_top {
      width: 100%;
      height: 0.26rem;
      color: #969799;
      font-size: 0.1rem;
      position: absolute;
      top: -0.26rem;
      left: 0;
      text-align: center;
    }
  }
}
.lwh_list {
  width: 100%;
  height: 2rem;
  box-sizing: border-box;
  padding: 0.1rem;
  > ul {
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 0.04rem;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 0.1rem;
    box-sizing: border-box;
    > li {
      width: 25%;
      text-align: center;
      > p {
        height: 0.36rem;
        line-height: 0.36rem;
        font-size: 0.12rem;
        color: #607075;
      }
      > div {
        margin: 0 auto;
        height: 0.42rem;
        width: 0.42rem;
        background: #fff;
        box-shadow: 0 0 3.2vw 0 rgba(10, 0, 24, 0.07);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
.lwh_cont {
  width: 100%;
  height: 2.5rem;
  background: #fff;
  box-sizing: border-box;
  padding: 0.15rem;
  > p {
    width: 100%;
    font-size: 0.16rem;
    margin-bottom: 0.15rem;
    color: #9f9f9f;
  }
  > div:nth-of-type(1) {
    width: 100%;
    height: 0.15rem;
    font-size: 0.14rem;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.25rem;

    > p:nth-of-type(2) {
      > span {
        display: none;
      }
    }
    > .lwh_default {
      color: orangered;
      > span {
        display: inline-block !important;
        > img {
          display: none;
        }
      }
      > .lwh_span_defaule {
        > img:nth-of-type(1) {
          display: block !important;
        }
        > img:nth-of-type(2) {
          display: none !important;
        }
      }
      > span {
        > img:nth-of-type(1) {
          display: none !important;
        }
        > img:nth-of-type(2) {
          display: block !important;
        }
      }
    }
  }
  > .lwh_main {
    text-align: center;
    > img {
      width: 0.92rem;
      height: 1.24rem;
      margin-bottom: 0.1rem;
    }
    > p {
      color: #9f9f9f;
      font-size: 0.12rem;
    }
  }
}
</style>
